// font family
$font-primary: "Poppins", sans-serif;
$font-secondary: "Montserrat", sans-serif;

// font colors
$color-primary: #ff014f;
$color-secondary: #f4f5f6;
$color-tertiary: #0d1013;
$color-gray: #f6f6f6;

$text-color: #C4CFDE;
$color-white: #ffffff;


// background colors
$background-color-1: linear-gradient(145deg, #1e2024, #23272b);
$background-color-2: #212428;
$background-color: #212428;
$background-color-hover: #434850;

// Gradient
$gradient-box-w: linear-gradient(145deg, #e2e8ec, #ffffff);
$gradient-red-hover: linear-gradient(145deg, #ff014f, #d11414);

// typo Color
$color-heading: #ffffff;
$color-heading-wv: #1e2125;
$color-body: #878e99;
$color-body-white: #3c3e41;
$color-lightn: #c4cfde;
$icon-color: #1BA1F2;

//Social icon colors
$color-facebook: #3B5997;
$color-twitter: #1BA1F2;
$color-youtube: #ED4141;
$color-linkedin: #0077B5;
$color-pinterest: #E60022;
$color-instagram: #C231A1;
$color-vimeo: #00ADEF;
$color-twitch: #6441A3;
$color-discord: #7289da;

// Font weight
$p-light: 300;
$p-regular: 400;
$p-medium: 500;
$p-semi-bold: 600;
$p-bold: 700;
$p-extra-bold: 800;
$p-black: 900;

// Font weight
$s-light: 300;
$s-regular: 400;
$s-medium: 500;
$s-semi-bold: 600;
$s-bold: 700;
$s-extra-bold: 800;
$s-black: 900;

//transition easing
$transition: 0.4s;

//Fonts Size
$font-size-b1: 18px;
$font-size-b2: 22px;
$font-size-b3: 14px;
$icon-size: 30px;
$font-size-section-heading: 36px;

//Line Height
$line-height-b1: 1.50;
$line-height-b2: 1.6;
$line-height-b3: 1.7;
// Heading Font
$h1: 70px;
$h2: 60px;
$h3: 50px;
$h4: 40px;
$h5: 30px;
$h6: 20px;

 // box-shadow
 $shadow-1: 10px 10px 19px #1c1e22,
 -10px -10px 19px #262a2e;
$shadow-2: inset 21px 21px 19px #181a1d, inset -21px -21px 19px #202225;
$inner-shadow: 1px 4px 2px -3px rgba(0, 0, 0, 0.7) inset, -1px -3px 3px -2px rgba(255, 255, 255, 0.2) inset;

$shadow-white-3: 5px 5px 15px #D1D9E6, -5px -5px 15px #ffffff;


// Gradient
$gradient-box-w: linear-gradient(145deg, #e2e8ec, #ffffff);
$gradient-red-hover: linear-gradient(145deg, #ff014f, #d11414);

